.main-title.kanban-1 {
  background-color: var(--kanban-green);
}
.main-title.kanban-2 {
  background-color: var(--kanban-yellow);
}
.main-title.kanban-3 {
  background-color: var(--kanban-blue);
}
//

.kanban-container {
  display: grid;
  grid-template-columns: repeat(3, minmax(min-content, 40px));
  overflow-x: auto;
  gap: 1rem;
  margin: 1rem;
  justify-content: start;
  transition: all 0.3s ease;
  background-color: var(--background);
}

.kanban-container .kanban.minimize {
  width: 40px;
  overflow: hidden;

  .main-title {
    justify-content: space-between;

    .touch-icon {
      display: none;
    }
  }
}

.kanban {
  background-color: var(--white);
  border-radius: 1rem;
  transition: all 0.3s ease;

  .main-title {
    cursor: pointer;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: var(--text-color);
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    h2 {
      font-size: 24px;
      transition: all 0.3s ease;

      @keyframes growAndShrink {
        0% {
          transform: scale(1);
        }
        50% {
          transform: scale(1.1);
        }
        100% {
          transform: scale(1);
        }
      }
      .touch-icon {
        padding: 0 10px;
        opacity: 0.8;
        font-size: 21px;
        animation: growAndShrink 2s infinite;
      }
    }
    .close-btn {
      cursor: pointer;
    }
    .status-icon {
      cursor: pointer;
    }
  }

  .columns-container {
    display: flex;
    justify-content: space-between;
    padding: 0 1rem;
    transition: all 0.3s ease;
  }

  .kanban-column {
    padding: 1rem;
    display: flex;
    background-color: var(--white);
    flex-direction: column;
    width: 100%;
    min-height: 70px;
    min-width: 300px;
    max-width: max-content;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    transition: all 0.3s ease;
    color: var(--text-color);

    &.kanban-column-drop-preview {
      background-color: var(--blue-background);
    }

    .kanban-tasks-list {
      min-height: 100px;
      border-radius: 4px;
      padding-top: 0.5rem;

      &.dragging-over {
        background-color: var(--blue-background);
      }
      &.can-drop {
        border: 2px dashed var(--blue);
      }
      transition: all 0.3s ease;
    }
  }
  .kanban-column.dragging-over {
    border: 1px dashed var(--blue);
    background-color: var(--blue-background);
    border-radius: 8px;
    margin-top: 1rem;
  }

  .kanban-task {
    background-color: var(--white);
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 8px;
    margin-bottom: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 300px;
    transition: all 0.3s ease;

    &.can-drop {
      border: 2px dashed var(--blue);
    }

    &:hover {
      background-color: var(--background-light);
    }

    .task-title-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 12px;
      gap: 1rem;
    }

    .task-content {
      display: flex;
      flex-direction: column;
      gap: 0.4rem;
      padding: 1.5rem 0;

      h3 {
        color: var(--blue);
        line-height: 1.3;
        font-size: 16px;
      }

      p {
        text-align: start;
        font-size: 14px;
      }
    }
  }

  &.minimize {
    .main-title {
      justify-content: space-between;
      padding: 1.3rem 0;
      height: 100%;
      flex-direction: column-reverse;

      h2 {
        transform: rotate(-90deg);
      }
    }

    .columns-container {
      min-height: 500px;
    }

    .kanban-column {
      background-color: transparent;

      h3 {
        display: none;
      }

      .kanban-tasks-list {
        display: none;
      }
    }
  }
}

.title-container {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  .sub-icon {
    font-size: 12px;
  }
  .material-symbols-outlined.sub-icon {
    font-variation-settings: "FILL" 1, "wght" 400, "GRAD" 0, "opsz" 24;
  }
}
