@import "./variables";

.hero-container {
  grid-column: container-start / container-end;
  background-color: var(--background);
  background-image: url(../imgs/clouds.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.dark-mode .hero-container {
  background-image: none;
}
.hero {
  display: flex;

  justify-content: center;
  align-items: center;
  gap: 7rem;
  padding: 14rem 4vw 2rem 4vw;
  // min-height: 20vh;
  color: var(--text-color);

  .right {
    display: flex;
    flex-direction: column;

    .hero-img {
      padding: 3rem;
      padding-bottom: 5rem;
      display: flex;
      width: 900px;
    }
    h1 {
      font-size: 7rem;
      font-weight: 600;
      z-index: 1;
      color: var(--text-link);
      height: 84px;
    }

    h1.span {
      letter-spacing: normal;
      padding-left: 3px;
    }

    p {
      font-size: 28px;
      font-weight: 200;
      margin-bottom: 2rem;
      z-index: 1;
      text-align: start;
      color: var(--text-color);
    }

    p .dot {
      background-color: var(--blue);
      width: 9px;
      height: 9px;
      margin-top: 5px;
      border-radius: 50%;
      display: inline-block;
    }
  }
  .left {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    .hero-img {
      width: 200px;
    }
    .btns-container {
      margin-top: 5rem;
    }
  }
}

@media screen and (max-width: 980px) {
  .hero {
    gap: 3rem;

    flex-direction: column;
    .right {
      text-align: center;
      p {
        text-align: center;
      }
    }
    .left {
      .btns-container {
        margin-top: 0;
      }
    }
  }
}
@media screen and (max-width: 800px) {
  .hero {
    .right {
      h1 {
        font-size: 5.3rem;
        height: 54px;
      }
      p {
        padding: 1rem 4rem;
      }
    }
  }
}
@media screen and (max-width: 600px) {
  .hero {
    .right {
      h1 {
        font-size: 4.5rem;
      }
    }
  }
}
@media screen and (max-width: 330px) {
  .hero {
    padding: 10rem 4vw 0 4vw;

    .right {
      h1 {
        font-size: 3.5rem;
      }
      p {
        font-size: 2rem;
      }
    }
  }
}
