@import "./variables";

.navbar {
  width: 100%;
  height: 8rem;
  line-height: 7rem;
  position: fixed;
  max-width: 2000px;
  padding: 0 3rem;
  background-color: var(--background-light);
  border-bottom: var(--text-color) solid 1px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  z-index: 99;

  .menu-icon {
    cursor: pointer;
    display: none;
    font-size: 40px;
    color: #498ba1;
  }
  img {
    display: flex;
    width: 80px;
  }

  .links {
    display: flex;
    gap: 2rem;
    align-items: center;
    padding-right: 30px;

    .link {
      cursor: pointer;
      color: var(--text-color);
      transition: all 0.3s ease-in-out;
    }
    .link:hover {
      color: var(--text-link);
    }
    .small-link {
      display: none;
    }
    .lang {
      display: flex;
      gap: 1rem;
      button {
        color: var(--text-color);
      }
      button.disabled {
        color: var(--text-color);
        opacity: 50%;
      }
    }
    .darkmode-toggle {
      color: var(--text-color);
    }
  }
}

@media screen and (max-width: 900px) {
  .navbar {
    .links {
      width: 300px;
      height: 100vh;
      background-color: var(--background-light);
      position: absolute;
      top: 8rem;
      left: -500px;
      transition: 300ms ease left;
      flex-direction: column;
      align-items: start;
      padding-left: 40px;
      gap: 0.1rem;
      &.active {
        left: 0px;
      }
      .small-link {
        display: flex;
      }
      .lang {
        display: flex;
        gap: 1rem;
        margin-top: 20px;
      }
      .darkmode-toggle {
        margin-top: 30px;
      }
    }

    .menu-icon {
      display: flex;
    }
  }
}

.about-flyout {
  background-color: var(--background-light);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  padding: 1rem;
  gap: 0.3rem;
  position: absolute;
  top: 0;
  margin-top: 60px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  a {
    height: 50px;
    margin-top: -18px;
  }
}
@media screen and (max-width: 900px) {
  .about-flyout {
    position: relative;
    box-shadow: none;
    margin-top: 0;
    justify-content: flex-start;
    align-items: start;
    padding: 0 1rem;
    background-color: var(--background);
  }
}
